iv>\n      </el-dialog>\n\n    </template>\n    <template>\n      <pagination class=\"page_center\"\n                  :pageCount='pageCount'\n                  @index='currentPage' />\n    </template>\n  </div>\n</template>\n<script>\nimport Viewer from '@/utils/Viewer'\nimport pagination from '@/components/pagination/pagination'\nexport default {\n  components: { pagination },\n  data () {\n    return {\n      token: window.sessionStorage.getItem('bbytoken'),\n      page: 1,//当前页\n      pageCount: 1,\n      pageSmCount: 1,\n      SmPage: 1,\n      pagestaffCount: 1,\n      staffPage: 1,\n      tableData: [],\n      addressVisible: false, //地址详情弹框状态\n      address: '', //详情地址,\n      ShopVisible: false, //店铺\n      shop_details: '',\n      ProfitVisible: false, //收益弹框状态\n      ProfitList: [],\n      sellVisible: false, //所售油品 \n      SellOut_Oils: [], //售出的油品详情\n      ReasonData: '',\n      ReasonVisible: false, //关停\n      cancelVisible: false, //取消合作\n      cancelData: '',\n      reason: '', //关停理由\n      oil_shop_id: '',\n      BankVisible: false,\n      BankData: {},\n      staffVisible: false,\n      staffData: [],\n      frequencyVisible: false,\n      frequencyData: [] \n    }\n  },\n\n  methods: {\n    async init () {\n      try {\n        let res = await this.$axios.post('admin/OilList/oilList', { token: this.token, page: this.page })\n        if (res.data.code == 1) {\n          this.tableData = res.data.data.list\n          this.pageCount = res.data.data.rows;\n        } else {\n          this.tableData = []\n        }\n      } catch (err) {\n        throw (err)\n      }\n    },\n    /* 收益详情数据 */\n    async Profit (row) {\n      try {\n        let res = await this.$axios.post('admin/OilList/incomeDetail', {\n          token: this.token,\n          id: row.id,\n          page: this.SmPage\n        })\n        if (res.data.code == 1) {\n          this.ProfitList = res.data.data.list\n          this.pageSmCount = res.data.data.rows\n        }\n      } catch